"use client"

import { useState, useEffect, useCallback } from "react"
import { AdminLayout } from "@/components/admin-layout"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { GameEntriesTable } from "@/components/game-entries-table" // Assuming this component exists
import { Sparkles, Loader2, AlertTriangle, Gamepad2 } from "lucide-react"

// Interfaces to match backend data structure
interface Game {
  id: string;
  created_at: string;
  range: number;
  status: "active" | "closed" | "revealed" | "completed";
  winning_number: number | null;
  title: string | null;
  description: string | null;
}

interface GameEntry {
  id: string;
  user_id: string;
  game_id: string;
  number: number;
  picked_at: string;
  userName: string; // This is the flattened name from the backend
}

export default function GameDetailsPage() {
  const [games, setGames] = useState<Game[]>([])
  const [selectedGameId, setSelectedGameId] = useState<string | null>(null)
  const [entries, setEntries] = useState<GameEntry[]>([])
  
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [revealing, setRevealing] = useState(false)

  const fetchGames = useCallback(async () => {
    setError(null)
    setLoading(true)
    try {
      const token = localStorage.getItem("sb_admin_token")
      const res = await fetch("/api/admin/games", {
        headers: { Authorization: `Bearer ${token}` },
      })
      if (!res.ok) throw new Error("Failed to fetch games.")
      const data: Game[] = await res.json()
      setGames(data)
      if (data.length > 0 && !selectedGameId) {
        setSelectedGameId(data[0].id)
      }
    } catch (err: any) {
      setError(err.message)
    } finally {
      setLoading(false)
    }
  }, [selectedGameId])

  useEffect(() => {
    fetchGames()
  }, [fetchGames])

  useEffect(() => {
    if (!selectedGameId) return

    const fetchEntries = async () => {
      try {
        const token = localStorage.getItem("sb_admin_token")
        const res = await fetch(`/api/admin/games/${selectedGameId}/entries`, {
          headers: { Authorization: `Bearer ${token}` },
        })
        if (!res.ok) throw new Error("Failed to fetch game entries.")
        const data: GameEntry[] = await res.json()
        setEntries(data)
      } catch (err: any) {
        setError(`Failed to load entries: ${err.message}`)
      }
    }
    fetchEntries()
  }, [selectedGameId])

  const selectedGame = games.find((g) => g.id === selectedGameId)

  const getStatusColor = (status: Game["status"]) => {
    switch (status) {
      case "active": return "bg-primary/20 text-primary border-primary/30"
      case "closed": return "bg-amber-500/20 text-amber-500 border-amber-500/30"
      case "revealed": return "bg-green-500/20 text-green-500 border-green-500/30"
      default: return "bg-zinc-500/20 text-zinc-500 border-zinc-500/30"
    }
  }

  const handleReveal = async () => {
    if (!selectedGameId) return
    setRevealing(true)
    setError(null)
    try {
      const token = localStorage.getItem("sb_admin_token")
      const res = await fetch("/api/lucky-grid/reveal", {
        method: "POST",
        headers: { Authorization: `Bearer ${token}` },
      })
      const data = await res.json()
      if (!res.ok) throw new Error(data.error || "Failed to reveal winner.")
      // Refetch all games to get the updated status
      await fetchGames()
    } catch (err: any) {
      setError(err.message)
    } finally {
      setRevealing(false)
    }
  }

  return (
    <AdminLayout>
      <div className="space-y-6">
        <div>
          <h1 className="text-2xl font-bold text-foreground">Game Details</h1>
          <p className="text-muted-foreground">View and manage lottery games</p>
        </div>

        {loading && (
          <div className="flex justify-center items-center p-10">
            <Loader2 className="h-8 w-8 animate-spin text-primary" />
          </div>
        )}

        {error && (
            <Alert variant="destructive">
              <AlertTriangle className="h-4 w-4" />
              <AlertTitle>Error</AlertTitle>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
        )}

        {!loading && !error && (
          games.length === 0 ? (
            <Card className="border-border bg-card text-center p-10">
                <Gamepad2 className="h-12 w-12 mx-auto text-muted-foreground" />
                <CardTitle className="mt-4">No Games Found</CardTitle>
                <p className="text-muted-foreground mt-2">Create a new game to get started.</p>
            </Card>
          ) : (
            <>
              <div className="max-w-xs">
                <Select value={selectedGameId ?? ""} onValueChange={setSelectedGameId}>
                  <SelectTrigger className="border-border bg-input text-foreground">
                    <SelectValue placeholder="Select a game" />
                  </SelectTrigger>
                  <SelectContent className="border-border bg-popover">
                    {games.map((game) => (
                      <SelectItem key={game.id} value={game.id}>
                        {game.title || "Untitled Game"} ({new Date(game.created_at).toLocaleDateString()})
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              {selectedGame && (
                <>
                  <Card className="border-border bg-card">
                    <CardHeader>
                      <div className="flex items-center justify-between">
                        <CardTitle className="text-card-foreground">{selectedGame.title || "Untitled Game"}</CardTitle>
                        <Badge variant="outline" className={getStatusColor(selectedGame.status)}>
                          {selectedGame.status.charAt(0).toUpperCase() + selectedGame.status.slice(1)}
                        </Badge>
                      </div>
                    </CardHeader>
                    <CardContent className="space-y-4">
                      <div className="grid gap-6 md:grid-cols-3">
                        <div>
                          <p className="text-sm text-muted-foreground">Description</p>
                          <p className="text-card-foreground">{selectedGame.description || "No description."}</p>
                        </div>
                        <div>
                          <p className="text-sm text-muted-foreground">Range</p>
                          <p className="text-card-foreground">1 - {selectedGame.range}</p>
                        </div>
                        <div>
                          <p className="text-sm text-muted-foreground">Created</p>
                          <p className="text-card-foreground">{new Date(selectedGame.created_at).toLocaleString()}</p>
                        </div>
                      </div>

                      {selectedGame.status === "revealed" && selectedGame.winning_number != null && (
                        <div className="rounded-lg bg-green-500/10 p-4 text-center">
                          <p className="text-sm text-green-400">Winning Number</p>
                          <p className="text-4xl font-bold text-green-400">{selectedGame.winning_number}</p>
                        </div>
                      )}

                      {selectedGame.status === "closed" && (
                        <Button onClick={handleReveal} className="bg-primary text-primary-foreground hover:bg-primary/90" disabled={revealing}>
                          {revealing ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Sparkles className="mr-2 h-4 w-4" />}
                          Reveal Winning Number
                        </Button>
                      )}
                      {selectedGame.status === "active" && (
                        <p className="text-sm text-muted-foreground">The game is currently active. You must close it before revealing a winner.</p>
                      )}
                    </CardContent>
                  </Card>

                  <Card className="border-border bg-card">
                    <CardHeader>
                      <CardTitle className="text-card-foreground">Entries ({entries.length})</CardTitle>
                    </CardHeader>
                    <CardContent>
                      {entries.length > 0 ? (
                        <GameEntriesTable entries={entries} winningNumber={selectedGame.winning_number} />
                      ) : (
                        <p className="text-center text-muted-foreground">No entries for this game yet.</p>
                      )}
                    </CardContent>
                  </Card>
                </>
              )}
            </>
          )
        )}
      </div>
    </AdminLayout>
  )
}
